<template>
<div>
    <div class="my-5 d-flex justify-content-center">
        <form class="col-md-6 " @submit.prevent="payOrder">
            <h2 class="mb-3 text-center">訂單資訊</h2>
            <table class="table ">
                <thead>
                    <th>品名</th>
                    <th>數量</th>
                    <th>單價</th>
                </thead>
                <tbody>
                    <tr v-for="item in order.products" :key="item.id">
                        <td class="align-middle">{{ item.product.title }}</td>
                        <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                        <td class="align-middle text-right">{{ item.final_total }}</td>
                    </tr>
                </tbody>
                <tfoot>
                    <tr>
                        <td colspan="2" class="text-right">總計</td>
                        <td class="text-right">{{ order.total }}</td>
                    </tr>
                </tfoot>
            </table>

            <table class="table">
                <tbody>
                    <tr>
                        <th width="100">Email</th>
                        <td>{{ order.user.email }}</td>
                    </tr>
                    <tr>
                        <th>姓名</th>
                        <td>{{ order.user.name }}</td>
                    </tr>
                    <tr>
                        <th>收件人電話</th>
                        <td>{{ order.user.tel }}</td>
                    </tr>
                    <tr>
                        <th>收件人地址</th>
                        <td>{{ order.user.address }}</td>
                    </tr>
                    <tr>
                        <th>付款狀態</th>
                        <td>
                            <span v-if="!order.is_paid">尚未付款</span>
                            <span v-else class="text-success">付款完成</span>
                        </td>
                    </tr>
                </tbody>
            </table>
            <div class="text-right" v-if="order.is_paid === false">
                <button class="btn btn-orange">確認付款去</button>
            </div>
        </form>
    </div>
</div>
</template>

<script>
export default {
    data() {
        return {
            products: [],
            orderId: '',
            order: {
                user: {} // 第二層的資料要預定義
            },
        }
    },
    methods: {
        getProducts() {
            const vm = this;
            const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPAH}/products`;
            vm.isLoading = true;
            this.$http.get(url).then((response) => {
                vm.products = response.data.products;
                //console.log(response);
                vm.isLoading = false;
            });
        },
        getOrder() {
            const vm = this;
            const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPAH}/order/${vm.orderId}`;
            vm.isLoading = true;
            this.$http.get(url).then((response) => {
                vm.order = response.data.order;
                console.log(response);
                vm.isLoading = false;
            });
        },
        payOrder() {
            const vm = this;
            const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPAH}/pay/${vm.orderId}`;
            vm.isLoading = true;
            this.$http.post(url).then((response) => {
                console.log(response);
                if (response.data.success) {
                    vm.getOrder();
                    vm.$router.push('/');
                    this.$bus.$emit('message:push', response.data.message, 'indigo');
                }
                vm.isLoading = false;
            });
        },
    },
    created() {
        this.orderId = this.$route.params.orderId;
        console.log(this.orderId);
        this.getOrder();
        this.getProducts();
    }
}
</script>

<style>
.table {
    border: 2px solid #6610f2;
}

.table thead th,
.table {
    border: 2px solid #6610f2;
}

.table th,
.table td {
    border: 2px solid #6610f2;
}
</style>
